import React, { useState } from "react"
import { Link } from "gatsby"

const Recipe = ({ recipe, location, containerTitle }) => {
  const { frontmatter, fields } = recipe
  const [loaded, handleImgLoaded] = useState(false)

  return (
    <li key={frontmatter.title}>
      <Link
        key={frontmatter.title}
        to={fields.slug}
        itemProp="url"
        state={{ prevPath: location.pathname, prevTitle: containerTitle }}
      >
        <section className="image">
          {!loaded ? <div className="skeleton"></div> : null}
          <img
            src={frontmatter.image}
            onLoad={() => handleImgLoaded(!loaded)}
            alt={frontmatter.title}
            style={{
              display: !loaded ? "none" : undefined,
            }}
          />
        </section>
        <section className="recipe-spec-container">
          <div>
            <h4>{frontmatter.title}</h4>
            <small className="author">{frontmatter.author}</small>
          </div>
          <div>
            <div className="separator-line"></div>
            <div className="recipe-spec">
              <div className="recipe-spec-time">
                <small className="spec-key">Tiempo</small>
                <small>{frontmatter.time}</small>
              </div>
              <div className="recipe-spec-portion">
                <small className="spec-key">Porci√≥n</small>
                <small>{frontmatter.portion || "-"}</small>
              </div>
            </div>
          </div>
        </section>
      </Link>
    </li>
  )
}

export default Recipe
