import React from "react"
import { Link } from "gatsby"

const Folders = ({ folders, location, title }) => {
  if (!folders) {
    folders = []
  }

  return (
    <ol className="folder-list">
      {folders.map(f => {
        return (
          <li key={f.name}>
            <Link
              key={f.name}
              to={f.path}
              itemProp="url"
              state={{ prevPath: location.pathname, prevTitle: title }}
            >
              <span>{f.name}</span>
            </Link>
          </li>
        )
      })}
    </ol>
  )
}

export default Folders
