import React, { useState } from "react"
import { graphql } from "gatsby"
import Select from "react-select"

import { constants } from "../../../constants"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import BackButton from "../../components/back-button"
import Folders from "../../components/folders"
import Recipes from "../../components/recipes"

const FILTER = {
  ingredients: "ingredients",
  author: "author",
}

const Cooking = ({ data, location }) => {
  const title = "Recetas"
  const path = constants.RECIPE_BASE_PATH
  const allRecipes = data.allMarkdownRemark.nodes

  const [recipes, handleRecipes] = useState(allRecipes)
  const [filters, handleFilters] = useState({})
  const [filtersExpanded, handleFiltersExpanded] = useState(false)

  const ingredientSelect = getIngredientSelect(allRecipes)
  const authorSelect = getAuthorSelect(allRecipes)

  const folders = [
    {
      name: constants.RECIPE_STARTERS.title,
      path: `${path}/${constants.RECIPE_STARTERS.folder}`,
    },
    {
      name: constants.RECIPE_SALADS.title,
      path: `${path}/${constants.RECIPE_SALADS.folder}`,
    },
    {
      name: constants.RECIPE_SOUPS.title,
      path: `${path}/${constants.RECIPE_SOUPS.folder}`,
    },
    {
      name: constants.RECIPE_MAIN_DISHES.title,
      path: `${path}/${constants.RECIPE_MAIN_DISHES.folder}`,
    },
    {
      name: constants.RECIPE_SAUCES.title,
      path: `${path}/${constants.RECIPE_SAUCES.folder}`,
    },
    {
      name: constants.RECIPE_BREADS.title,
      path: `${path}/${constants.RECIPE_BREADS.folder}`,
    },
    {
      name: constants.RECIPE_DESSERTS.title,
      path: `${path}/${constants.RECIPE_DESSERTS.folder}`,
    },
    {
      name: constants.RECIPE_OTHERS.title,
      path: `${path}/${constants.RECIPE_OTHERS.folder}`,
    },
  ]

  return (
    <Layout location={location} title={title}>
      <SEO title={title} />
      <h1>{title}</h1>

      <div className={filtersExpanded ? "back-filter expanded" : "back-filter"}>
        <BackButton location={location} path="/wiki" title="Wiki" />
        <button
          className={filtersExpanded ? "active" : ""}
          onClick={() => handleFiltersExpanded(!filtersExpanded)}
        >
          <span className={filtersExpanded ? "active" : "disabled"}>
            &#x21BF;
          </span>
          <span className={filtersExpanded ? "disabled" : "active"}>
            &#x21C3;
          </span>
          Filtros
        </button>
      </div>

      {filtersExpanded && (
        <div className="cooking-filter">
          <div className="select-author">
            {filters.author && <span className="filter-name">Autor:</span>}
            <Select
              isClearable
              placeholder="Autor"
              options={authorSelect}
              onChange={(v, { action }) => {
                const f = { ...filters }
                let r = allRecipes
                if (action === "select-option") {
                  f.author = v.value
                  r = recipes
                } else {
                  delete f[FILTER.author]
                }

                handleFilters(f)
                handleRecipes(
                  filterRecipes({
                    recipes: r,
                    filters: f,
                  })
                )
              }}
              theme={selectTheme}
            />
          </div>

          <div className="select-ingredients">
            {filters.ingredients && filters.ingredients.length > 0 && (
              <span className="filter-name">Ingredientes:</span>
            )}
            <Select
              closeMenuOnSelect={false}
              isMulti
              placeholder="Ingredientes"
              options={ingredientSelect}
              onChange={(v, { action }) => {
                const f = { ...filters, ingredients: v.map(i => i.value) }

                handleFilters(f)
                handleRecipes(
                  filterRecipes({
                    recipes: action === "select-option" ? recipes : allRecipes,
                    filters: f,
                  })
                )
              }}
              theme={selectTheme}
            />
          </div>
        </div>
      )}

      {!isFiltering(filters) && (
        <Folders folders={folders} location={location} title={title} />
      )}
      {isFiltering(filters) && (
        <Recipes recipes={recipes} location={location} containerTitle={title} />
      )}
    </Layout>
  )
}

const isFiltering = filters =>
  (filters.ingredients && filters.ingredients.length > 0) || filters.author

const selectTheme = theme => ({
  ...theme,
  borderRadius: "var(--radius-1)",
  colors: {
    ...theme.colors,
    primary25: "var(--color-folders)",
    primary: "var(--color-text-light)",
  },
})

const getIngredientSelect = recipes => {
  const ingredients = {}

  for (let r of recipes) {
    if (r.frontmatter.ingredients) {
      for (let i of r.frontmatter.ingredients) {
        if (!ingredients[i]) {
          ingredients[i] = i
        }
      }
    }
  }

  return getSelectOptions(ingredients)
}

const getAuthorSelect = recipes => {
  const authors = {}

  for (let r of recipes) {
    const author = r.frontmatter.author
    if (author && !authors[author]) {
      authors[author] = author
    }
  }

  return getSelectOptions(authors)
}

const getSelectOptions = obj => {
  const result = []

  for (let k in obj) {
    result.push({ value: k, label: k })
  }

  return result.sort((a, b) => a.value > b.value)
}

const filterRecipes = ({ recipes, filters }) =>
  recipes.filter(r => {
    for (let k in filters) {
      if (k === FILTER.ingredients) {
        if (!filters[k].every(i => r.frontmatter.ingredients?.includes(i))) {
          return false
        }
      } else if (k === FILTER.author) {
        if (filters[k] !== r.frontmatter.author) {
          return false
        }
      }
    }

    return true
  })

export default Cooking

export const recipeListQuery = graphql`
  query {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: {
        frontmatter: { draft: { ne: true } }
        fileAbsolutePath: { regex: "/cooking/" }
      }
      limit: 1000
    ) {
      nodes {
        fields {
          slug
        }
        frontmatter {
          date(formatString: "YYYY-MM-DD")
          title
          author
          image
          time
          portion
          ingredients
        }
      }
    }
  }
`
